import React from "react";
import ProductCard from "../components/ProductCard";
import { data } from "../data";
import styled from "styled-components";
import { useDispatch } from "react-redux";
import { addToCartAction } from "../actions";

const Container = styled.div`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
  margin: 0 auto;
  padding: 2rem;
`;

const ProductList = () => {
  const dispatch = useDispatch();
  const handleAddToCart = (phoneId, memory, count) => {
    dispatch(addToCartAction(phoneId, memory, count));
  };
  return (
    <Container>
      {data.map((phoneSeries) => {
        return (
          <ProductCard
            key={phoneSeries.id}
            imgSrc={phoneSeries.image}
            title={phoneSeries.name}
            models={phoneSeries.models}
            phoneId={phoneSeries.id}
            onAddToCart={handleAddToCart}
          />
        );
      })}
    </Container>
  );
};

export default ProductList;
