import React, { useMemo, useState } from "react";
import Card from "./Card";

import styled from "styled-components";
import NumberInput from "./NumberInput";
import Button from "./Button";

const BodyContainer = styled.div`
  select {
    margin: 1rem 0;
  }
`;

const PriceContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
`;

const ProductCard = ({ imgSrc, title, models, phoneId, onAddToCart }) => {
  const [count, setCount] = useState(1);
  const [selectedMemory, setSelectedMemory] = useState(models[0].memory);

  const handleSelectModel = (e) => {
    const { value } = e.target;
    setSelectedMemory(value);
  };

  const handleAddToCard = () => {
    onAddToCart(phoneId, selectedMemory, count);
  };

  const selectedPrice = models.find(
    (model) => model.memory === Number(selectedMemory)
  ).price;

  const selectedTotal = count * selectedPrice;

  //   const selectedTotal = useMemo(() => {
  //     const price = models.find((model) => model.memory === Number(selectedModel))
  //       .price;
  //     return count * price;
  //   }, [count, selectedModel, models]);
  return (
    <Card imgSrc={imgSrc} title={title}>
      <BodyContainer>
        <select value={selectedMemory} onChange={handleSelectModel}>
          {models.map((model) => {
            return (
              <option key={model.memory} value={model.memory}>
                {`${model.memory} GB - $${model.price}`}
              </option>
            );
          })}
        </select>
        <NumberInput
          onIncrease={() => setCount(count + 1)}
          onDecrease={() => count > 1 && setCount(count - 1)}
          value={count}
        />
        <PriceContainer>
          <div>{selectedTotal}</div>
          <Button onClick={handleAddToCard}>Add to cart</Button>
        </PriceContainer>
      </BodyContainer>
    </Card>
  );
};

export default ProductCard;
