import React from "react";
import styled from "styled-components";
import Button from "./Button";
import NumberInput from "./NumberInput";

const CartItem = ({
  count,
  memory,
  phoneId,
  className,
  price,
  name,
  onIncrease,
  onDecrease,
  onRemove
}) => {
  return (
    <div className={className}>
      <div className="info">
        <div>Name: {name}</div>
        <div>Memory: {memory}GB</div>
        <NumberInput
          value={count}
          onIncrease={() => onIncrease(phoneId, 1, Number(memory))}
          onDecrease={() => onDecrease(phoneId, -1, Number(memory))}
        />
      </div>
      <div className="price-container">
        <div>Total price: ${price * count}</div>
        <Button onClick={() => onRemove(phoneId, memory)}>Remove</Button>
      </div>
    </div>
  );
};

export default styled(CartItem)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 1rem;
  border-radius: 8px;
  margin-top: 16px;
  .info {
    display: flex;
    justify-content: space-around;
    align-items: center;
    div {
      margin-left: 1rem;
    }
  }
  .price-container {
    div {
      margin-bottom: 1rem;
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
`;
