import React from "react";
import { useSelector } from "react-redux";
import CartItem from "../components/CartItem";
import { useDispatch } from "react-redux";
import { addToCartAction, checkout, removeItemFromCart } from "../actions";
import Button from "../components/Button";

const Cart = () => {
  const cart = useSelector((state) => {
    return state.cart.cart;
  });

  const dispatch = useDispatch();

  const handleRemove = (phoneId, memory) => {
    dispatch(removeItemFromCart(phoneId, memory));
  };

  const handleIncrease = (phoneId, memory) => {
    dispatch(addToCartAction(phoneId, memory, 1));
  };

  const handleDecrease = (phoneId, memory, currentCount) => {
    if (currentCount === 1) {
      dispatch(removeItemFromCart(phoneId, memory));
    } else {
      dispatch(addToCartAction(phoneId, memory, -1));
    }
  };

  const handleCheckout = () => {
    dispatch(checkout());
  };

  const cartArr = Object.values(cart).filter((item) => Boolean(item));
  const total = cartArr.reduce((acc, item) => {
    const itemPrice = item.price * item.count;
    return acc + itemPrice;
  }, 0);
  return (
    <div>
      {cartArr.map((item) => {
        return (
          <CartItem
            count={item.count}
            memory={item.memory}
            phoneId={item.phoneId}
            price={item.price}
            name={item.name}
            onIncrease={() => handleIncrease(item.phoneId, item.memory)}
            onDecrease={() =>
              handleDecrease(item.phoneId, item.memory, item.count)
            }
            onRemove={() => handleRemove(item.phoneId, item.memory)}
          />
        );
      })}
      <div>Total: {total}</div>
      <Button onClick={handleCheckout}>Checkout</Button>
    </div>
  );
};

export default Cart;
