import * as Actions from "./actions";
import { data } from "./data";

const createProductId = (phoneId, memory) => `${phoneId}|${memory}`;

const initialState = {
  cart: {
    // 'phoneId + memory': {
    //     phoneId,
    //     price,
    //     name,
    //     count,
    //     memory
    // }
  }
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case Actions.ADD_TO_CART: {
      const { phoneId, count, memory } = action.payload;
      const phoneSeries = data.find(
        (phoneSeries) => phoneSeries.id === phoneId
      );
      const { name } = phoneSeries;
      const model = phoneSeries.models.find(
        (model) => model.memory === Number(memory)
      );
      const productId = createProductId(phoneId, model.memory);
      const { price } = model;

      return {
        ...state,
        cart: {
          ...state.cart,
          [productId]: state.cart[productId]
            ? {
                ...state.cart[productId],
                count: state.cart[productId].count + count
              }
            : {
                phoneId,
                price,
                name,
                count,
                memory
              }
        }
      };
    }

    case Actions.REMOVE_FROM_CART: {
      const { phoneId, memory } = action.payload;
      const productId = createProductId(phoneId, memory);
      return {
        ...state,
        cart: {
          ...state.cart,
          [productId]: undefined
        }
      };
    }

    case Actions.CHECKOUT: {
      return {
        ...state,
        cart: {}
      };
    }
    default:
      return state;
  }
};

export default reducer;
